import React, { useState } from "react";
import DeckGL from "@deck.gl/react";
import { Map } from "react-map-gl";
import { HexagonLayer } from "deck.gl";
import { useAppContext } from "../context/AppContext";
import "mapbox-gl/dist/mapbox-gl.css";
import { AmbientLight, PointLight, LightingEffect } from "@deck.gl/core";
import RangeSlider from "react-bootstrap-range-slider";
import { Form, Col, Row } from "react-bootstrap";

const MAPBOX_ACCESS_TOKEN =
  "pk.eyJ1IjoicGV0ZWhpbGxqbnIiLCJhIjoiY2swcjZtd3IxMDJjOTNjb3c1Z25wczQ3NyJ9.2Ut4kmGeVuvc7UUb-qezNw";

const ambientLight = new AmbientLight({
  color: [255, 255, 255],
  intensity: 1.0,
});

const initialViewState = {
  longitude: 176.6,
  latitude: -40.1,
  zoom: 9,
  pitch: 60,
  bearing: 0,
};

const pointLight1 = new PointLight({
  color: [255, 255, 255],
  intensity: 0.8,
  position: [-0.144528, 49.739968, 80000],
});

const pointLight2 = new PointLight({
  color: [255, 255, 255],
  intensity: 0.8,
  position: [-3.807751, 54.104682, 8000],
});

const lightingEffect = new LightingEffect({
  ambientLight,
  pointLight1,
  pointLight2,
});

const material = {
  ambient: 0.64,
  diffuse: 0.6,
  shininess: 32,
  specularColor: [51, 51, 51],
};

const MAP_STYLE = "mapbox://styles/mapbox/dark-v9";

const colorRange = [
  [1, 152, 189],
  [73, 227, 206],
  [216, 254, 181],
  [254, 237, 177],
  [254, 173, 84],
  [209, 55, 78],
];

const MapContainer = () => {
  const { state } = useAppContext();
  const [opacity, setOpacity] = useState(0.8);
  const [radius, setRadius] = useState(500);
  const [assetTypes, setAssetTypes] = useState([]);

  const filteredData =
    assetTypes.length === 0 ? state.map_data : state.map_data.filter(d => assetTypes.includes(d.asset_type));

  const hexLayer = {
    id: "hexmapWeighted",
    //colorRange,
    data: filteredData,
    coverage: 0.9,
    elevationScale: 5,
    extruded: true,
    getPosition: (d) => [d.x, d.y],
    pickable: true,
    //autoHighlight: true,
    //highlightColor: [0, 255, 0],
    radius: radius,
    upperPercentile: 100,
    lowerPercentile: 0,
    opacity: opacity,
    visible: true,
    material,
    getColorWeight: (d) => d.value,
    colorAggregation: "SUM",
    getElevationWeight: (d) => d.value,
    elevationAggregation: "SUM",
  };

  const layer = new HexagonLayer(hexLayer);
  
  const handleSelectedMultiple = evt => {
    const values = Array.from(evt.target.selectedOptions, option => option.value);
    setAssetTypes(values)    
  };

  return (
    <>
      <div style={{ height: "600px", width: "100%", position: "relative" }}>
        <DeckGL
          layers={[layer]}
          //effects={[lightingEffect]}
          initialViewState={initialViewState}
          controller={true}
        >
          <Map mapboxAccessToken={MAPBOX_ACCESS_TOKEN} mapStyle={MAP_STYLE} />
        </DeckGL>
      </div>
      <div>
        <Form>
          <Row>
            <Form.Group as={Col} className="mb-3">
              <Form.Label>Opacity</Form.Label>
              <RangeSlider
                value={opacity}
                onChange={(e) => setOpacity(e.target.value)}
                min={0}
                max={1}
                step={0.05}
                size={"sm"}
              />
            </Form.Group>
            <Form.Group as={Col}>
              <Form.Label>Radius (m)</Form.Label>
              <RangeSlider
                value={radius}
                onChange={(e) => setRadius(e.target.value)}
                min={100}
                max={10000}
                step={50}
                size={"sm"}
              />
            </Form.Group>
            <Form.Group as={Col}>
              <Form.Label>Asset Type</Form.Label>
              <Form.Control
                as="select"
                multiple
                value={assetTypes}
                onChange={handleSelectedMultiple}
              >
                {Object.keys(state.data).map((d) =>  (
                  <option key={`${d}-map-filter`} value={d}>
                    {d}
                  </option>
                ))}
              </Form.Control>
            </Form.Group>
          </Row>
        </Form>
      </div>
    </>
  );
};

export default MapContainer;
