import React from "react";
import { Container, Row } from "react-bootstrap";
import { useAppContext } from "../context/AppContext";
import Controller from "./Controller";

const Controls = (props) => {
  const context = useAppContext();
  const { asset_type } = props;
  const { state  } = context;

  const fields = Object.keys(state.data[props.asset_type].controllers);

  return (
    <Container style={{ marginTop: "0.5rem" }}>
      <Row style={{ marginTop: "0.5rem " }}>
        <h2>Data Weightings</h2>
      </Row>
      <hr />
      {fields.map((c) => (
        <div key={`${asset_type}-${c}-holder`}>
          <Row key={c}>
            <Controller field={c} asset_type={asset_type} key={`controller-${asset_type}-${c}`} />
          </Row>
          <hr />
        </div>
      ))}
    </Container>
  );
};
export default Controls;
