import React, { useState, useEffect } from "react";
import RangeSlider from "react-bootstrap-range-slider";
import { useAppContext } from "../context/AppContext";
import { useDebounce } from "../utilities/useDebounce";

import { Row, Col, Container, Button, ButtonGroup, ButtonToolbar } from "react-bootstrap";
const Slider = (props) => {
  const context = useAppContext();
  const { state, dispatch } = context;
  const { asset_type, field, option, canApply } = props;

  const value = state.data[asset_type].controllers[field].controls[option];
  const [sliderValue, setSliderValue] = useState(value);

  const debouncedTerm = useDebounce(sliderValue, 750);

  const updateField = () => {
    dispatch({
      type: "update_controller_value",
      payload: {
        asset_type,
        field,
        option,
        value: debouncedTerm,
      },
    });
  };
  useEffect(() => {
    if (debouncedTerm) {
      updateField();
    }
  }, [debouncedTerm]);

  const handleChange = (e) => {
    setSliderValue(e.target.value);
    /*
    dispatch({
      type: "update_controller_value",
      payload: {
        asset_type,
        field,
        option,
        value: e.target.value,
      },
    });
    */
  };

  const handleClick = () => {
    dispatch({
      type: "remove_controller",
      payload: {
        asset_type,
        field,
        option,
      },
    });
  };

  return (
    <Container style={{ marginTop: "0.5rem" }}>
      <Row>
        <Col md={{ span: 3 }}>
          <h5 className="float-end">{option}</h5>
        </Col>
        <Col md={{ span: 4 }}>
          <RangeSlider
            value={sliderValue}
            onChange={handleChange}
            min={0}
            max={5}
            step={0.1}
            size={"sm"}
          />
        </Col>
        <Col md={{ span: 1 }}>
          <span className="float-start">{value}</span>
        </Col>
        <Col md={{ span: 4 }}>
          <ButtonToolbar>
            <ButtonGroup>
              <Button style={{ marginLeft: "0.2rem", marginRight: "0.2rem" }} variant="danger" size="md" onClick={handleClick}>
                Remove
              </Button>
            </ButtonGroup>
            
          </ButtonToolbar>
        </Col>
      </Row>
    </Container>
  );
};

export default Slider;
