const getControllers = (dispatches, asset_type) => {
  let controllers = {
    onrc: {
      title: "ONRC",
      values: [...new Set(dispatches.map((d) => d.onrc).sort())],
      controls: {},
      canApply: true,
    },
    days_since: {
      title: "Days Since",
      values: [...new Set(dispatches.map((d) => d.days_since).sort())],
      controls: {},
      canApply: true,
    },
    asset_type_desc: {
      title: "Asset Type",
      values: [...new Set(dispatches.map((d) => d.asset_type_desc).sort())],
      controls: {},
      canApply: false,
    },
    fault_type: {
      title: "Fault Type",
      values: [...new Set(dispatches.map((d) => d.fault_desc).sort())],
      controls: {},
      canApply: false,
    },
    priority_desc: {
      title: "Priority",
      values: [...new Set(dispatches.map((d) => d.priority_desc).sort())],
      controls: {},
      canApply: true,
    },
    call_type_desc: {
      title: "Call Type",
      values: [...new Set(dispatches.map((d) => d.call_type_desc).sort())],
      controls: {},
      canApply: true,
    },
    programme_category_desc: {
      title: "Programme Category",
      values: [
        ...new Set(dispatches.map((d) => d.programme_category_desc).sort()),
      ],
      controls: {},
      canApply: true,
    },
    programme_desc: {
      title: "Programme Name",
      values: [...new Set(dispatches.map((d) => d.programme_desc).sort())],
      controls: {},
      canApply: true,
    },
  
  };

  // read from local storage the existing controls
  Object.keys(controllers).forEach(c => {
    const existing = localStorage.getItem(`${asset_type}-controls-${c}`);
    const output = existing ? JSON.parse(existing) : {};

    controllers[c].controls = output;
  })

  return controllers;
};

export default getControllers;
