const reducer = (state, action) => {
  const { type, payload } = action;
  console.log(type);
  switch (type) {
    case "update_deep_field":
      let group = state[payload.group];
      group = { ...group, [payload.field]: payload.value };
      return {
        ...state,
        [payload.group]: group,
      };

    case "add_controller":
      const existingControls =
        state.data[payload.asset_type].controllers[payload.field].controls;

      state.data[payload.asset_type].controllers[payload.field].controls = {
        ...existingControls,
        [payload.option]: payload.value,
      };

      state.map_data = mapScores(state.data);
      
      localStorage.setItem(
        `${payload.asset_type}-controls-${[payload.field]}`,
        JSON.stringify(state.data[payload.asset_type].controllers[payload.field].controls || {})
      );

      return { ...state };

    case "update_controller_value":
      const existing =
        state.data[payload.asset_type].controllers[payload.field].controls[
          payload.option
        ];

      state.data[payload.asset_type].controllers[payload.field].controls[
        payload.option
      ] = payload.value;

      if (payload.value !== existing) {
        state.data[payload.asset_type].data = calculateScores(
          state.data[payload.asset_type].data,
          state.data[payload.asset_type].controllers
        );

        state.map_data = mapScores(state.data);
      }
      localStorage.setItem(
        `${payload.asset_type}-controls-${payload.field}`,
        JSON.stringify(state.data[payload.asset_type].controllers[payload.field].controls || {})
      );
      return { ...state };

    case "remove_controller":
      //console.log(state);
      delete state.data[payload.asset_type].controllers[payload.field].controls[
        payload.option
      ];

      state.data[payload.asset_type].data = calculateScores(
        state.data[payload.asset_type].data,
        state.data[payload.asset_type].controllers
      );

      state.map_data = mapScores(state.data);

      localStorage.setItem(
        `${payload.asset_type}-controllers`,
        JSON.stringify(state.data[payload.asset_type].controllers)
      );
      return {
        ...state,
      };
    default:
      return { ...state };
  }
};

const calculateScores = (data, controllers) => {
  console.log("calculating scores");
  data.forEach((d) => {
    let score = 1;
    Object.keys(controllers).forEach((control) => {
      let value = d[control];
      const multiplier = controllers[control].controls[value] || 1;
      d["score_" + control] = parseFloat(multiplier);
      score = score * parseFloat(multiplier);
    });
    d.score = score;
  });

  return data;
};

const mapScores = (data, points) => {
  console.log("calculating map");
  const allData = Object.keys(data).reduce((a, b) => {
    const pts = data[b].data
      .map((d) =>
        d.point_list.map((p) => {
          return {
            ...p,
            asset_type: d.asset_type_desc,
            value: d.score / d.point_list.length,
          };
        })
      )
      .reduce((i, j) => i.concat(j), []);
    return a.concat(pts);
  }, []);

  return allData;
};

export { calculateScores, mapScores };

export default reducer;
