import React from "react";
import { useAppContext } from "../context/AppContext";
import { Container, Form, Row, Col, Button } from "react-bootstrap";
import { useState } from "react";
import Slider from "./Slider";

const Controller = (props) => {
  const context = useAppContext();

  const { state, dispatch } = context;
  const { field, asset_type } = props;

  const { title, values, controls, canApply } = state.data[asset_type].controllers[field];

  const [value, setValue] = useState(values[0]);

  const clickHandler = () => {
    dispatch({
      type: "add_controller",
      payload: { asset_type, field: field, option: value, value: 1 },
    });
  };

  return (
    <Container>
      <Row>
        <Col md={{ span: 2}}>
          <h3>{title}</h3>
        </Col>
        <Col md={{ span: 5}}>
          <div className="input-group">
            <Form.Select
              style={{ width: "20%" }}
              value={value}
              onChange={(e) => setValue(e.target.value)}
            >
              {values.map((m) => {
                return (
                  <option key={`${field}-${m}`} value={m}>
                    {m}
                  </option>
                );
              })}
            </Form.Select>
            <Button variant="primary" onClick={clickHandler}>
              Add Option
            </Button>
          </div>
        </Col>
      </Row>
      <Row>
        <Col>
          {Object.keys(controls).map((c) => (
            <Slider key={`slider-${asset_type}-${field}-${c}`} field={field} option={c} asset_type={asset_type} canApply={canApply} />
          ))}
        </Col>
      </Row>
    </Container>
  );
};

export default Controller;
