import dispatches from "../sql/dispatchData.json";
import getControllers from "./getControllers";
import getColumns from "./getColumns";
import { calculateScores, mapScores } from "./reducer";
import points from "../sql/points.json";

const base_columns = getColumns();

dispatches.forEach((d) => {
  d.score = 1;

  d.point_list = points
    .filter(
      (p) =>
        p.road_id === d.road_id &&
        p.start_m < d.map_end &&
        p.end_m > d.map_start
    )
    .map((p) => {
      const minc = d.map_start > p.start_m ? d.map_start : p.start_m;
      const maxc = d.map_end < p.end_m ? d.map_end : p.end_m;

      return {
        system_id: p.system_id,
        coverage: (maxc - minc) / (p.end_m - p.start_m),
        x: p.x,
        y: p.y
      };
    });
});

const pointHashMap = points.reduce(function (map, obj) {
  map[obj.system_id] = { x: obj.x, y: obj.y };
  return map;
});

const assetTypes = [
  ...new Set(dispatches.map((d) => d.asset_type_desc).sort()),
];

let initialData = {};

assetTypes.forEach((d) => {
  const assetData = dispatches.filter((a) => d === a.asset_type_desc);
  const controllers = getControllers(assetData, d);
  const controllerValues = Object.keys(controllers);

  assetData.forEach((e) => {
    controllerValues.forEach((f) => {
      e[`score_${f}`] = 1;
    });
  });

  let s = [];

  controllerValues.forEach((f) => {
    s.push({
      id: `score_${f}`,
      name: `score_${f}`,
      selector: (row) => row[`score_${f}`],
      sortable: true,
    });
  });

  initialData = {
    ...initialData,
    [d]: {
      data: calculateScores(assetData, controllers),
      controllers,
      columns: base_columns.concat(s),
    },
  };
});

const initialState = {
  data: initialData,
  map_data: mapScores(initialData, pointHashMap),
  points: pointHashMap
};

const getInitialState = () => {
  console.log("Initial state called", initialState)
  return initialState;
};

export default getInitialState;
