import React from "react";
import HeaderBar from "./HeaderBar";
import { Container, Tabs, Tab } from "react-bootstrap";
import { useAppContext } from "../context/AppContext";
import Datatables from "./Datatables";
import Controls from "./Controls";
import MapContainer from "./MapContainer";

export default function Main() {
  const context = useAppContext();
  const { state } = context;
  
  return (
    <>
      <HeaderBar />
      <Container style={{ marginTop: "0.5rem" }}>
        <Tabs className="mb-3" variant="pills">
          {Object.keys(state.data).map((d) => {
            return (
              <Tab eventKey={d} title={d} key={`tab-${d}`}>
                <h1>{d}</h1>
                <Datatables asset_type={d} key={`data-table-${d}`} />
                <Controls asset_type={d} key={`controls-${d}`} />
              </Tab>
            );
          })}
          <Tab eventKey="map-control" title="Fault Map" key="tab-fault-map">
            <Container>
              <MapContainer />
            </Container>
          </Tab>
        </Tabs>
      </Container>
    </>
  );
}
